import React, { Component } from 'react'
import { Form, Input, Select,Button } from 'antd'
const FormItem = Form.Item, Option = Select.Option
class FilterValue extends Component {
    state = {
        statusList: [
            { label: "", value: "", color: "" },
            // { label: "", value: "1", color: "rgba(65,100,228,0.2)" },
            { label: "", value: 1, color: "rgba(250,84,28,0.3)" },
            { label: "", value: 2, color: "rgba(250,219,20,0.3)" },
            { label: "", value: 3, color: "rgba(82,196,26,0.3)" },
            { label: "", value: 4, color: "rgba(250,84,28,0.3)" },
        ],
        effectList: [
            { label: "", value: "" },
            { label: "", value: 1 },
            { label: "", value: 2 },
        ]
    }
    componentDidMount(){
        document.addEventListener("keydown",this._onkeydown);
    }
    _onkeydown = (e) => {
        if(e.keyCode === 13){
            this.handleSearch();
        }
    }
    componentWillUnmount(){
        document.removeEventListener("keydown",this._onkeydown)
    }
    handleSearch=()=>{
        this.props.form.validateFields((err, values) => {
            if (!err) {
                // console.log(values);
                this.props.setfilterValue(values)
            }
        });
    }
    handleReset=()=>{
        this.props.form.resetFields();
        this.props.setfilterValue({
            formName:'',
            taskStatus:'',
            status:'',
        }, 'reset')
    }

    render() {
        const { getFieldDecorator } = this.props.form
        return (
            <Form layout='inline'>
                <FormItem label=''>
                    {
                        getFieldDecorator('formName', {
                            initialValue: this.props.filterValue.formName || ''
                        })(<Input placeholder='' />)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('taskStatus', {
                            initialValue: this.props.filterValue.taskStatus || ''
                        })(<Select style={{ width: 160 }}>
                            {
                                this.state.statusList.map((item,index) => <Option key={index} value={item.value}>{item.label}</Option>)
                            }
                        </Select>)
                    }
                </FormItem>
                <FormItem label=''>
                    {
                        getFieldDecorator('status', {
                            initialValue: this.props.filterValue.status || ''
                        })(<Select style={{ width: 160 }}>
                            {
                                this.state.effectList.map((item,index) => <Option key={index} value={item.value}>{item.label}</Option>)
                            }
                        </Select>)
                    }
                </FormItem>
                <FormItem>
                    <Button onClick={this.handleSearch} type='primary' ghost icon='search' style={{marginRight: 12}}></Button>
                    <Button icon="sync" onClick={this.handleReset}></Button>
                </FormItem>
            </Form>
        )
    }
}

export default Form.create({
    onValuesChange:(props, changedValues, allValues)=>{
        props.setfilterValue(allValues,'notSearch')
    }
})(FilterValue)
