import React, { Component } from 'react';
import { getAction,postAction } from '@/axios';
import { Button, Tree, message, Modal, Input } from 'antd';
import styles from './index.module.less';
import {hasButtonPermission} from '@/util'
const { TreeNode } = Tree;
// const MyIcon = Icon.createFromIconfontCN({
//     scriptUrl:"//at.alicdn.com/t/font_1331187_ydzjg7bsu2.js"
// })

export default class FolderManagement extends Component {
    constructor(props){
        super(props);
        this.state = {
            modalType:"add",
            folderName:"",
            showModal:false,///
            selectedKeys:[],//key
            selectedNodes:[],//
            defaultExpandedKeys:["0000"],//

            treeData:[
                {
                    folderName:"",
                    formFolderId:"0000",
                    children:[]
                }
            ]
        }
    }
    componentDidMount(){
        this.queryFolderTree();
    }
    componentDidUpdate(prepProps){
        if(JSON.stringify(prepProps) !== JSON.stringify(this.props)){
            this.setState({selectedKeys: []})
        }
    }
    //
    queryFolderTree=()=>{
        getAction('/ybt/form/folder/queryFolderTree').then(res=>{
            if(res.success){
                let treeData = JSON.parse(JSON.stringify(this.state.treeData));
                treeData[0].children = res.obj||[]
                this.setState({
                    treeData:treeData||[]
                });  
            }else{
                message.error(res.obj)
            }
        });
    }
    //
    handleAddFolder = () => {
        // console.log(this.state.selectedKeys);
        // 
        let folders = this.state.treeData[0].children;
        // console.log(folders);
        let data = folders.filter((item)=>{
            return item.formFolderId===this.state.selectedKeys[0]
        });
        //
        if(data.length>0 || !this.state.selectedKeys[0] ||this.state.selectedKeys[0]==="0000"){
            this.setState({showModal:true,modalType:"add",folderName:""})
        }else{
            message.warn("");
        } 
    }
    //
    handleOpenEdit = () => {
        if(this.state.selectedNodes.length > 0){
            this.setState({showModal:true,modalType:"edit",folderName:this.state.selectedNodes[0].props.title})
        }else{
            message.warn("")
        }
    }
    //
    handleChangeFolderName = (e) => {
        this.setState({folderName:e.target.value})
    }
    
    //
    handleOk = () => {
        if(this.state.folderName===""){
            message.warn("")
        }else{
            this.OperateFolderReq();
        }
    }
    OperateFolderReq=()=>{
        let url = "";
        let msg = "";
        let data = "";
        // console.log(this.state.selectedKeys[0]);
        let keys = this.state.selectedKeys;
        let parentFolderId = keys[0]?keys[0]==="0000"?"":keys[0]:"";
        if(this.state.modalType==="add"){
            url = "/ybt/form/folder/createFolder";
            msg = "";
            data = {
                folderName:this.state.folderName,
                parentFolderId:parentFolderId,
                remark:"",
                delFlag:0
            }
        }
        if(this.state.modalType==="edit"){
            url = "/ybt/form/folder/updateFolderById";
            msg = "";
            data = {
                folderName:this.state.folderName,
                formFolderId:parentFolderId,
                remark:"",
                updateBy:"asd"
            }
        }
        postAction(url,data).then(res=>{
            if(res.success){
                message.info(msg);
                this.queryFolderTree();
                this.setState({showModal:false})
            }else{
                message.error(res.obj)
                this.setState({showModal:false})
            }
        }); 
    }
    //
    handleDelete = () => {
        if(this.state.selectedNodes.length > 0){
            Modal.confirm({
                title:"?",
                onOk:()=>{
                    //
                    getAction('/ybt/form/folder/deleteFolderById',{formFolderId:this.state.selectedKeys[0]}).then(res=>{
                        if(res.success){
                            message.info("");
                            this.queryFolderTree();
                        }else{
                            message.error(res.obj)
                        }
                    });
                },
                okText:"",
                cancelText:""
            })  
        }else{
            message.warn("")
        }
    }
    renderTree = (data=[]) => data.map(item => {
        if(item.children && item.children.length>0){
            return <TreeNode title={item.folderName} key={item.formFolderId}>
                {this.renderTree(item.children)}
            </TreeNode>
        }else{
            return <TreeNode title={item.folderName} key={item.formFolderId}></TreeNode>
        }
    })
    onSelect = (selectedKeys, e) => {
        if(e.selected){
            // console.log(e.selectedNodes);
            this.setState({ selectedKeys,selectedNodes:e.selectedNodes});

            // this.props.changeSelectedFolder(e.selectedNodes[0])
            this.props.changeSelectKey(selectedKeys[0])
        }else{
            return false
        }
        
    }
    render() {
        let param = this.state.selectedNodes;
        let operateBtnStatu=param.length > 0?param[0].key==="0000"?true:false:true;
        // console.log(operateBtnStatu);
        return (
            <div className={styles.folderContainer}>
                <div className={styles.buttonContainer}>
                    {
                        hasButtonPermission('form001')&&
                        <Button icon="plus" type="primary" onClick={this.handleAddFolder} size="small"></Button>

                    }
                    {
                        hasButtonPermission('form003') && 
                        <Button style={{margin:"0px 5px"}} onClick={this.handleOpenEdit} disabled={operateBtnStatu} size="small"></Button>

                    }
                    {
                        hasButtonPermission('form002') && 
                        <Button onClick={this.handleDelete} disabled={operateBtnStatu} size="small"></Button>

                    }
                </div>
                <div className={styles.folderTree}>
                    <Tree
                        onSelect={this.onSelect}
                        selectedKeys={this.state.selectedKeys}
                        defaultExpandedKeys={this.state.defaultExpandedKeys}
                    >
                        {this.renderTree(this.state.treeData)}
                    </Tree>
                </div>
                <Modal
                    visible={this.state.showModal}
                    title={this.state.modalType==="add"?"":""}
                    onOk={this.handleOk}
                    onCancel={()=>{this.setState({showModal:false})}}
                    width={400}
                >
                    <div>
                        
                        <Input style={{width:290}} placeholder="" value={this.state.folderName} onChange={this.handleChangeFolderName}/>
                    </div>
                </Modal>
            </div>
        )
    }
}
