import React, { Component } from 'react';
import { Card,message } from 'antd';
import FolderManagement from './folderManagement';
import FolderContent from './folderContent';
import styles from './index.module.less';
import FilterValue from './filterValue';
import { postAction } from '@/axios';
class FormManagement extends Component {
    state = {
        filterValue: {
            formName:'',
            taskStatus:'',
            status:'',
        },
        selectKey:'',
        tableData:[],
        pagination:{
            total:0,         //
            current:1,
            pageSize:10,
            showTotal:(total, range)=> ` ${total} `,//
            showQuickJumper:true,//
            showSizeChanger:false,//
        }
    }
    componentDidMount(){
        this.getTableData();
    }
    //
    setfilterValue=(filterValue,type)=>{
        if(type === 'reset') {
            this.changeSelectKey('');
            this.setState({filterValue},this.getTableData)
        }else if(type === 'notSearch'){
            this.setState({ filterValue })
        }else{
            this.setState({filterValue},this.getTableData)
        }
    }
    //key
    changeSelectKey=(key)=>{
        this.setState({
            selectKey:key
        },this.getTableData)
    }
    //table
    getTableData = (page) => {
        let current = 1;
        if(page){
            current = page;
        }
        let filterValue = {
            ...this.state.filterValue,
            formFolderId:this.state.selectKey?this.state.selectKey==="0000"?"":this.state.selectKey:""
        }
        let url = `/ybt/form/list?pageNum=${current}&pageSize=${this.state.pagination.pageSize}`;
        postAction(url,filterValue).then(res=>{
            if(res.success){
                // 
                let pagination = {
                    ...this.state.pagination,
                    total:res.obj.total,
                    current:res.obj.page
                }
                let tableData = res.obj.rows||[];
                tableData.forEach((item,index)=>{
                     item.sortIndex = index+1;
                });
                this.setState({
                    tableData,
                    pagination
                })
            }else{
                message.error(res.obj)
            }
        });
    }
    render() {
        return (
            <div style={{ padding: 20 }}>
                <Card title={<FilterValue  filterValue={this.state.filterValue}  setfilterValue={this.setfilterValue} />} bodyStyle={{ padding: "0px" }}>

                    <div className={styles.container}>
                        <FolderManagement changeSelectKey={this.changeSelectKey}/>
                        <div className={styles.splitLine}></div>
                        <FolderContent data={this.state.tableData} pagination={this.state.pagination} changePage={this.getTableData}/>
                    </div>
                </Card>
            </div>
        )
    }
}

export default FormManagement