import React, { Component } from 'react';
// import moment from 'moment';
import {getAction,postAction } from '@/axios';
import { withRouter } from 'react-router-dom';
import { Button, Switch, Table, Popconfirm, Divider, message, Modal, TreeSelect, Input } from 'antd';
import styles from './index.module.less';
import PrvilegeModal  from './prvilegeModal';
import {hasButtonPermission} from '@/util'
import StepComponent from './stepComponent';
import PreviewDoc from '@/pages/common/PreviewDoc';
const TreeNode = TreeSelect.TreeNode;
const statusList=[
    { label: "", value: "", color: "" },
    // { label: "", value: "1", color: "rgba(65,100,228,0.2)" },
    { label: "", value: "1", color: "#9254DE" },
    { label: "", value: "2", color: "#FADB14" },
    { label: "", value: "3", color: "#FA541C" },
    { label: "", value: "4", color: "#52C41A" },
]
 class FolderContent extends Component {
    constructor(props){
        super(props);
        this.state = {
            
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:"60px",
                    fixed:"left",
                },{
                    title:"",
                    dataIndex:"formName",
                    width:"390px",
                    render:(text,record)=><div className='operation-link' onClick={()=>{this._previewForm(record)}} title={text}>{text}</div>
                },{
                    title:"",
                    dataIndex:"formUseType",
                    width:"100px",
                    render:(text)=>{
                        if(Number(text)===1){return ""}
                        if(Number(text)===2){return ""}
                        if(Number(text)===3){return ""}
                    }
                },{
                    title:"",
                    dataIndex:"name",
                    width:"100px",
                },{
                    title:"",
                    dataIndex:"departName",
                    width:"120px",
                },{
                    title:"",
                    dataIndex:"updateTime",
                    width:"160px",
                    // render:text=> text?moment.unix(text/1000).format("YYYY/MM/DD HH:mm:ss"):""
                },{
                    title:"",
                    dataIndex:"times",
                    width:"110px",
                },{
                    title:"",
                    dataIndex:"taskStatus",
                    width:"100px",
                    render:(text,record)=>{
                        const status = statusList.find(item => item.value===text);
                        return status?
                        <span style={{cursor:'pointer'}} onClick={()=>{this.queryOverTaskProcessRequest(record)}}><i style={{display:"inline-block",width:8,height:8,borderRadius:4,marginRight:6,background:status.color}}></i>{status.label}</span>
                        :""
                    }
                },{
                    title:"",
                    dataIndex:"status",
                    width:"120px",
                    render:(text,record) => (hasButtonPermission('form008')?(<Switch checkedChildren="" unCheckedChildren="" 
                                                checked={text==="1"?true:false} 
                    onChange={(val,e) =>{this.handleToggle(e,val,record)}} />):null)
                },{
                    title:"",
                    width:"160px",
                    fixed:"right",
                    render:(text,record) => {
                        if(record.taskStatus==="1"){
                            return <React.Fragment>
                                {
                                    hasButtonPermission('form004') && 
                                    <React.Fragment>
                                        <span className="operation-link" onClick={()=>{this.props.history.push(`/formManagement/createForm?id=${record.formId}`)}}></span>
                                        <Divider type="vertical"/>
                                    </React.Fragment>
                                }
                                {
                                    hasButtonPermission('form007') && 
                                    <Popconfirm
                                        title=""
                                        onConfirm={()=>{this.handleDelete(record)}}
                                        okText=""
                                        cancelText=""
                                    >
                                        <span className="operation-link"></span>
                                        <Divider type="vertical"/>
                                    </Popconfirm>
                                }
                                    {
                                        hasButtonPermission('form010') && 
                                        <React.Fragment>
                                            <span className="operation-link" onClick={()=>{this._previewPdf(record)}}></span>
                                            <Divider type="vertical"/>
                                        </React.Fragment>
                                    }
                                    {
                                        hasButtonPermission('form011') && 
                                        <span className="operation-link" onClick={()=>{this._previewWorkFlow(record)}}></span>
                                    }
                                </React.Fragment>
                        } else if(record.taskStatus==="3" || record.taskStatus==="4"){////
                            if(record.times===0){
                                return <React.Fragment>
                                    {
                                        hasButtonPermission('form004') && 
                                        <React.Fragment>
                                            <span className="operation-link" onClick={()=>{this.props.history.push(`/formManagement/createForm?id=${record.formId}`)}}></span>
                                            <Divider type="vertical"/>
                                        </React.Fragment>
                                    }
                                    {
                                        record.taskStatus==="4"&&record.status==="1"?<React.Fragment>
                                        {
                                            hasButtonPermission('form005') && 
                                            <React.Fragment>
                                                <span className="operation-link" onClick={()=>{this._copy(record)}}></span>
                                                <Divider type="vertical"/>
                                            </React.Fragment>
                                        }
                                        {
                                            hasButtonPermission('form006') && 
                                            <React.Fragment>
                                                <span className="operation-link" onClick={()=>{this._prvilege(record)}}></span>
                                                <Divider type="vertical"/>
                                            </React.Fragment>
                                        }       
                                        </React.Fragment>:null
                                    }
                                    {
                                        hasButtonPermission('form007') && 
                                        <Popconfirm
                                            title=""
                                            onConfirm={()=>{this.handleDelete(record)}}
                                            okText=""
                                            cancelText=""
                                        >
                                            <span className="operation-link"></span>
                                            <Divider type="vertical"/>
                                        </Popconfirm>
                                    }
                                    {
                                        hasButtonPermission('form010') && 
                                        <React.Fragment>
                                            <span className="operation-link" onClick={()=>{this._previewPdf(record)}}></span>
                                            <Divider type="vertical"/>
                                        </React.Fragment>
                                    }
                                    {
                                        hasButtonPermission('form011') && 
                                        <span className="operation-link" onClick={()=>{this._previewWorkFlow(record)}}></span>
                                    }
                                </React.Fragment>
                            }else{
                                return <React.Fragment>
                                   {
                                    hasButtonPermission('form004') && 
                                    <React.Fragment>
                                        <span className="operation-link" onClick={()=>{this.props.history.push(`/formManagement/createForm?id=${record.formId}`)}}></span> 
                                    </React.Fragment>
                                    }
                                    {
                                        record.taskStatus==="4"&&record.status==="1"?
                                        <React.Fragment>
                                            {
                                                hasButtonPermission('form005') && 
                                                <React.Fragment>
                                                    <Divider type="vertical"/>
                                                        <span className="operation-link" onClick={()=>{this._copy(record)}}></span>
                                                </React.Fragment>
                                            }
                                            {
                                                hasButtonPermission('form006') && 
                                                <React.Fragment>
                                                    <Divider type="vertical"/>
                                                    <span className="operation-link" onClick={()=>{this._prvilege(record)}}></span>
                                                    <Divider type="vertical"/>
                                                </React.Fragment>
                                            } 
                                        </React.Fragment>:null
                                    }
                                    {
                                        hasButtonPermission('form010') && 
                                        <React.Fragment>
                                            <span className="operation-link" onClick={()=>{this._previewPdf(record)}}></span>
                                            <Divider type="vertical"/>
                                        </React.Fragment>
                                    }
                                    {
                                        hasButtonPermission('form011') && 
                                        <span className="operation-link" onClick={()=>{this._previewWorkFlow(record)}}></span>
                                    }
                                </React.Fragment>
                            }
                        }else{//
                            return ""
                        }
                    }
                }
            ],
            showCopyModal:false,//
            treeData:[],//
            formName:"",//
            formFolderId:"",//
            currentFormInfo:{},//
            showPrvilegeModal:false,//
            showProcessModal:false,
            stepsData:null,
            showPDF:false,
            pdfRecord:{},
        }
    }
    componentDidMount(){
        this._getFolderTreeRequest();
    }
    //
    queryOverTaskProcessRequest=(record)=>{
        if(!record.processId){
            message.info('');
            return false
        }
        this.setState({showProcessModal:true})
        let data={
            businessId:record.formId,
            processId:record.processId
        }
        getAction('ybt/planTask/queryProcessMap',data).then(res => {
            if(res.success){
                //setps
                let stepsData = {current:res.obj.current,steps:res.obj.setps}
                this.setState({
                    stepsData
                });
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    closeProcessModal = () => {
        this.setState({
            showProcessModal:false,
            stepsData:null
        })
    }
    //
    _previewPdf = (record) => {
        this.setState({
            showPDF:true,
            pdfRecord:record
        });
    }
    //
    _previewForm = (record) => {
        window.open(`${window.location.origin}/#/formManagement/previewForm?id=${record.formId}&type=preview`)
    }
    //
    _previewWorkFlow = (record) => {
        window.open(`${window.location.origin}/#/formManagement/previewWorkFlowContainer?id=${record.fillProcessId}`)
    }
    //
    _prvilege = (record) => {
        this.setState({
            showPrvilegeModal:true,
            currentFormInfo:record
        })
    }
    //
    _closePrivilegeModal = () => {
        this.setState({
            currentFormInfo:{},
            showPrvilegeModal:false,
        })
    }
    //
    _updatePrivilege = (data) => {
        // console.log(data);
        postAction("/ybt/form/addFormPerm",data).then(res => {
            if(res.success){
                this.setState({
                    currentFormInfo:{},
                    showPrvilegeModal:false
                });
                this.props.changePage();
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _getFolderTreeRequest = () => {
        getAction('/ybt/form/folder/queryFolderTree').then(res=>{
            if(res.success){
                this.setState({
                    treeData:res.obj||[]
                });  
            }else{
                message.error(res.obj)
            }
        });
    }
    //
    onChange=(pagination)=>{
        this.props.changePage(pagination.current)
    }
    //
    handleToggle = (e,val,record) => {
        let data = {
            formId:record.formId,
            status:val?1:2
        }
        getAction("/ybt/form/updateFormIsEffect",data).then(res=>{
            if(res.success){
                message.success("")
                this.props.changePage();
            }else{
                message.error(res.obj)
            }
        }); 
    }
    //
    handleDelete = (record) => {
        // console.log(record);
        let data = {
            formId:record.formId,
            taskStatus:record.taskStatus,
            times:record.times
        }
        getAction("/ybt/form/deleteFormById",data).then(res=>{
            if(res.success){
                message.success("");
                this.props.changePage();
            }else{
                message.error(res.obj)
            }
        }); 
    }
    //
    _copy = (record) => {
        this.setState({
            currentFormInfo:record,
            showCopyModal:true,
            formName:"",
            formFolderId:""
        })
        // console.log(record)
    }
    
    //
    _cancle = () => {
        this.setState({
            currentFormInfo:{},
            showCopyModal:false,
            formName:"",
            formFolderId:""
        })
    }
    //
    _renderFolderTree = (data=[]) => data.map(item => {
        if(item.children && item.children.length>0){
            return <TreeNode value={item.formFolderId} title={item.folderName} key={item.formFolderId}>
                {this._renderFolderTree(item.children)}
            </TreeNode>
        }else{
            return <TreeNode value={item.formFolderId} title={item.folderName} key={item.formFolderId}></TreeNode>
        }
    });
    //
    _changeName = (e) => {
        this.setState({formName:e.target.value})
    }
    //
    handleChangeFolder = (value) => {
        this.setState({formFolderId:value})
    }
    //ok
    _copyRequest = () => {
        let params = {
            formId:this.state.currentFormInfo.formId,
            formName:this.state.formName,
            formFolderId:this.state.formFolderId,
        }
        postAction("/ybt/form/copy",params).then(res=>{
            if(res.success){
                message.success("");
                this._cancle();
                this.props.changePage();
            }else{
                message.error(res.obj)
            }
        })
    }
    render() {
        
        return (
            <div className={styles.formContainer}>
                <div className={styles.filterBox}>                    
                    <div className={styles.filterItem}>
                        {
                            hasButtonPermission('form009') &&
                            <Button icon="plus" type="primary" 
                                onClick={()=>{this.props.history.push("/formManagement/createForm")}}
                            >
                                
                            </Button>
                        }
                        
                    </div>
                </div>
                <div className={styles.table}>
                    <Table
                        rowKey="sortIndex"
                        columns={this.state.columns}
                        dataSource={this.props.data}
                        scroll={{x:"1420px"}}
                        pagination={this.props.pagination}             //
                        onChange={this.onChange}
                    />
                </div>
                <Modal
                    visible={this.state.showCopyModal}
                    title=""
                    onCancel={this._cancle}
                    onOk={this._copyRequest}
                    destroyOnClose={true}
                    width={520}
                >
                    <div style={{marginBottom:"10px"}}>
                        <label style={{display:"inline-block",width:120}}>:</label>
                        <Input style={{width:330}} placeholder="" value={this.state.formName} onChange={this._changeName}/>
                    </div>
                    <div>
                        <label style={{display:"inline-block",width:120}}></label>
                        <TreeSelect
                             style={{width:330}}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            placeholder=""
                            allowClear
                            // showSearch
                            // treeNodeFilterProp="title"
                            treeDefaultExpandAll
                            value={this.state.formFolderId}
                            onChange={this.handleChangeFolder}
                        >
                            {this._renderFolderTree(this.state.treeData)}
                        </TreeSelect>
                    </div>
                </Modal>
                <Modal
                    visible={this.state.showPrvilegeModal}
                    title=""
                    bodyStyle={{paddingTop:"5px"}}
                    width={800}
                    destroyOnClose={true}
                    onCancel={this._closePrivilegeModal}
                    footer={false}
                >
                    <PrvilegeModal
                        currentFormInfo={this.state.currentFormInfo}
                        onCancel={this._closePrivilegeModal}
                        onOk={(data)=>{this._updatePrivilege(data)}}
                    />
                </Modal>
                <Modal
                    visible={this.state.showProcessModal}
                    width={800}
                    title=''
                    destroyOnClose={true}
                    onCancel={()=>{this.setState({showProcessModal:false,stepsData:null})}}
                    footer={false}
                >
                    <StepComponent data={this.state.stepsData}/>
                </Modal>
                <Modal
                    visible={this.state.showPDF}
                    title={<span style={{fontWeight:"bold"}}></span>}
                    width={1200}
                    bodyStyle={{padding:0,height:"70vh"}}
                    destroyOnClose={true}
                    footer={false}
                    onCancel={()=>{this.setState({showPDF:false})}}
                >
                    <PreviewDoc id={this.state.pdfRecord.formId} type='formId'/>
                </Modal>
            </div>
        )
    }
}

export default withRouter(FolderContent)